import React from 'react';
import classes from './DrawToogler.css'
const DrawerToggle = (props) => {
    return (
        <div onClick={ props.clicked} className={classes.DrawerToggle}>
            <div></div>
            <div></div>
            <div></div>
        </div>
    );
};

export default DrawerToggle;



